.HeroContainer {
    background: #191919 /*#42f5bf*/;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
    height: 720px;
    width: 100%;
    position: relative;
    z-index: 1;

}
 

.HeroBackground {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 90vh;
    overflow: hidden;

}


.HeroContent {
    z-index: 3;
    max-width: 1200px;
    position: absolute;
    padding: 8px 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.HeroH1 {
    color: white;
    font-size: 48px;
    text-align: center;
    margin: 0px;
}

.HeroP {
    margin-top: 24px;
    color: white;
    /*#e85d45;*/
    font-size: 24px;
    text-align: center;

}



.HeroLogo {
    width: 20rem;
    height: 20rem;
    margin-bottom: 24px;
}

@media screen and (max-width: 768px) {

    .HeroLogo {
        width: 15rem;
        height: 15rem;
    }
    
    .HeroP {
        font-size: 18px;
    }

    .HeroH1 {
        font-size: 32px;
    }
}

@media screen and (max-width: 480px) {

    .HeroLogo {
        width: 12rem;
        height: 12rem;
    }

    .HeroP {
        font-size: 16px;
    }

    .HeroH1 {
        font-size: 28px;
    }
}