nav {
  background: #191919;
  height: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  position: sticky;
  align-items: center;
  top: 0;
  z-index: 10;

  box-shadow: 0 2px 4px -1px rgba(0,0,0,0.4),0 4px 5px 0 rgba(0,0,0,0.3),0 1px 10px 0 rgba(0,0,0,0.2);

}

.NavbarContainer {
  display: flex;
  justify-content: space-between;
  height: 52px;
  z-index: 1;
  width: 100%;
  padding: 0px 24px;
  max-width: 1100px;
}

.NavH1 {
  color: #5fc2af;
  justify-self: flex-start;
  cursor: pointer;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  font-weight: bold;
  text-decoration: none;
}

.MobileLogo {
  display:none;

}

.NavMenu {
  display: flex;
  align-items: flex-end;
  list-style: none;
  text-align: left;

}



.NavItem {
    color: white;
    display: flex;
    align-items: center;
    text-align: none;
    text-decoration: none;
    padding: 0 1rem;
    height: 100%;
    cursor: pointer;
}

@media screen and (max-width:768px) {
    .MobileLogo {
        display: flex;
        align-items: center;
        list-style: none;
        text-align: left;
        color: #5fc2af;
        font-size: 1.8rem;
        margin-bottom: 2px;
    }
    .NavMenu {
        display: none;
    }
  }

.NavLink {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

}

.NavLink.active {
  border-bottom: 3px solid #54ac9a;
}
