.AboutContainer {
    background: #5fc2af;
    display: flex;
    flex-direction: column;
    padding: 30px;
    height: 560px;
    width: 100%;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 1;

}

.AboutContent {
    z-index: 3;
    min-height: 240px;
    width: 1140px;
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    background: none;
}

.AboutSection {
    height: 320px;
    width: 570px;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    background: none;;

}


.AboutText {
    z-index: 3;
    width: 540px;
    position: absolute;
    flex-direction: column;
    padding: 8px 24px;
    display: block;
    align-items: center;
    background: none;

}

.AboutH1 {
    color: white;
    font-size: 48px;
    margin-top: 24px;
    text-align: center;
    background: none;
}

.AboutP {
    margin: 24px;
    color: white;
    font-size: 24px;
    text-align: center;
    background: none;
}


.AboutArt {
    margin: 24px;
    width: 360px;
    z-index:3;
    background: none;
}


@media screen and (max-width: 1140px) {
    .AboutContainer {
        height: 720px;
    }
    .AboutContent {
        width:100%;
        flex-direction: column !important;
    }

    .AboutSection {
        width:960px;
    }
    .AboutText {
        width: 960px;
    }
}

@media screen and (max-width: 960px) {
    .AboutText {
        width: 540px;
    }

    .AboutSection {
        width:540px;
    }


    .AboutArt {
    width: 320px;
    }
}

@media screen and (max-width: 540px) {
    .AboutText {
        width: 390px;
    }
    
    .AboutSection {
        width:390px;
    }
}
