.ContactMe {
    background: #191919;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0px 28px 28px 28px;;
    margin: 0px;
    height: 680px;
    width: 100%;
    position: relative;
    z-index: 0;

}



.ContactContent {
    z-index: 3;
    height: 540px;
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

.Title {
    font-size: 3rem;
}

input {
    height:3rem;
    width: 540px;
    border-radius: 12px;
    -webkit-appearance: none; 
    -moz-appearance: none; 
    appearance: none;
    background-clip: padding-box;
    box-shadow: none !important;
    border:none;
    outline:none;
    padding: 1rem;
    box-sizing: border-box;
    font-size:12pt;
    margin: 8px;
    z-index: 7;
    background: white;
}


textarea {
    height:120px;
    width: 540px;
    resize: none;
    border-radius: 12px;
    -webkit-appearance: none; 
    -moz-appearance: none; 
    appearance: none;
    background-clip: padding-box;
    box-shadow: none !important;
    border:none;
    outline:none;
    padding: 1rem;
    box-sizing: border-box;
    font-size:12pt;
    margin: 8px;
    z-index: 7;
    background: white;
}

button {
    height:3rem;
    width: 540px;
    border-radius: 12px;
    -webkit-appearance: none; 
    -moz-appearance: none; 
    appearance: none;
    background-clip: padding-box;
    box-shadow: none !important;
    border:none;
    outline:none;
    padding: 1rem;
    box-sizing: border-box;
    font-size:12pt;
    margin: 8px;
    z-index: 7;
    background: #5fc2af;

    
}

button:active {
    background: #54ac9a;
}

.successBox {
    background: rgb(110, 190, 110);
    display: none;
    color: white;
    height:2.5rem;
    width: 540px;
    border-radius: 12px;
    text-align: center;
    justify-content: center;
    align-items: center;
    padding: 8px;
    margin: 8px;
    position: relative;
}

.success > .successBox {
    display: flex !important;
}

.errorBox {
    background: rgb(196, 111, 111);
    display: none;
    color: white;
    height:3rem;
    width: 540px;
    border-radius: 12px;
    text-align: center;
    justify-content: center;
    align-items: center;
    padding: 8px;
    margin: 8px;
    position: relative;
}

.error > .errorBox {
    display: flex !important;
}


@media screen and (max-width: 768px) {

    .Title {
        font-size: 1.5rem;
    }
    input {
        width: 100%;
    }

    textarea{
        width:100%;
    }

    button {
        width: 100%;
    }
    
    .successBox {
        width: 100%;
    }

    .errorBox {
        width: 100%;
    }

}


