.CourseWork {
    background: #191919;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 56px 28px 56px 28px;;
    margin: 0px;
    height: 1180px;
    width: 100%;
    position: relative;
    z-index: 1;
}



.CourseContent {
    z-index: 3;
    height: 900px;
    width: 1140px;
    position: relative;
    display: grid;
    grid-gap: 24px;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
    justify-content: center;

}

.CourseBlock {
    height:256px;
    width:1fr;
    background: #191919;
    box-shadow: 0 2px 4px -1px rgba(0,0,0,0.4),0 4px 5px 0 rgba(0,0,0,0.3),0 1px 10px 0 rgba(0,0,0,0.2);
    display: block;
    color: white;
    border-top: 3px solid red;
    justify-content: center;
    text-align:center;
}

.Language {
    color: #707070;
    font-size: 1.75rem;
    bottom: 0px;
    margin-top:64px;
}

.CourseBlock.Software-Dev {
    border-top: 3px solid rgb(129, 90, 151);
}

.CourseBlock.Web-Dev {
    border-top: 3px solid rgb(88, 156, 156);
}

.CourseBlock.Game-Dev {
    border-top: 3px solid rgb(151, 84, 84);
}

.CourseBlock.Computer-Eng {
    border-top: 3px solid rgb(101, 141, 77);
}

.CourseBlock.SQL {
    border-top: 3px solid #a17131;
}

.CourseH1 {
    color: white;
    font-size: 1.25rem;
    margin: 24px 0px;
    text-align: center;
    width: 100%;
}

.CourseP {
    margin-top: 1em;
    color: white;
    font-size: 1em;
    text-align: center;
}

h1.Software-Dev {
    color:rgb(129, 90, 151);
}

.CourseH1.Web-Dev {
    color: rgb(88, 156, 156);
}

.CourseH1.Game-Dev {
    color: rgb(151, 84, 84);
}

.CourseH1.Computer-Eng {
    color: rgb(101, 141, 77);
}

.CourseH1.SQL {
    color: #a17131;
}

.Title {
    font-size: 3rem;
    color: white;
}


@media screen and (max-width: 1200px) {

    .CourseContent {
        width: 960px;
    }
}

@media screen and (max-width: 1080px) {

    .CourseContent {
        width: 720px;
        grid-template-columns: 1fr 1fr;
        height: 1480px;
    }


    .CourseWork {
        height: 1680px;
    }

    .Title {
        font-size: 2.2rem !important;
    }
}

@media screen and (max-width: 768px) {

    .CourseContent {
        width: 480px;
        grid-template-columns: 1fr;
        height: 2680px;
    }
    .CourseWork {
        height: 2900px;
    }

    .Title {
        font-size: 1.5rem!important;
    }

}

@media screen and (max-width: 540px) {

    .CourseContent {
        width: 320px;
        grid-template-columns: 1fr;
        height: 2680px;
    }
    .CourseWork {
        height: 2900px;
    }
}
